@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin rotate($rotate) { 
  transform: rotate($rotate);
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}

.collection-page {
	position: relative;
	.collection-title {
		.h3 {
			line-height: 1.4em;
			font-weight: 500;
		}
		.collection-count {
			font-size: inherit;
		}
	} 
	.main-collection {
		margin-top: 30px;
	} 
	#mainProducts {
		overflow: hidden;
	}
	.flex-list-1, .flex-list-2 {		
		.card {
			margin-bottom: 20px;
		}
	}
	.collection-empty {
		margin: 0;
	}
}

.collection-toolbar {
	padding-bottom: 15px;
	margin-bottom: 30px;
	border-bottom: 1px solid var(--miscellaneous-color);
	>.flex {
		margin: 0 -10px;
		>div {
			padding: 5px 10px;
			width: 100%;
			justify-content: space-between;
		}
	}
	collection-filter {
		>.flex {
			padding: 15px;
			@media (min-width: 750px) {
				margin: 0 -10px;
				padding: 20px;
			}
			>div {
				&:not(:last-child) {
					padding-bottom: 20px;
					margin-bottom: 20px;
					border-bottom: 1px solid var(--miscellaneous-color);
				}
				@media (min-width: 750px) {
					padding: 0 10px;
					&:not(:last-child) {
						padding-bottom: 0;
						margin-bottom: 0;
						border-bottom: 0;
					}
				}
			}
		}
		&.active {
			box-shadow: 0 0px 4px rgba(0,0,0,0.07);
		}
	}
	@media (min-width: 750px) {
		>.flex {
			>div {
				width: auto;
				justify-content: unset;
			}
		}
	}
}
.collection-filter-button {
	@include margin-end(30px);
	button {
		padding: 8px 25px;
		font-weight: 500;
		font-size: inherit;
		color: inherit;
	}
	svg {
		width: 16px;
		height: 16px;
		@include margin-end(13px);
	}
}
.collection-paginate {
	@media (min-width: 360px) {
		@include margin-start(30px);
	}
	span {
		@include padding-end(10px);
	}
	.show-title {
		color: var(--subtext-color);
		font-weight: 400;
	}
	.show-label {
		opacity: 0;
		visibility: hidden;
	}
	svg {
		path {
			fill: var(--text-color);
		}
	}
	details {
		min-width: 70px;
		>summary {
			line-height: 40px;
			flex-wrap: nowrap;
		}
		>div {
			left: 0;
			right: 0;
			z-index: 100;
			background: var(--background-color);
			box-shadow: 0px 0px 4px rgba(0,0,0,0.15);
			border-radius: var(--border-radius);
			padding: 10px 0;
			margin-top: 10px;
		}
	}
	.collection-paginate-list {
		li {
			padding: 5px 15px;
			font-weight: 400;
			transition: all .25s ease-in-out;
		}
		li {
			&:hover, &.active {
				button {
					color: var(--primary-color);
				}
			}
		}
	}
	#collection-paginate-list-select {
		@include ab(0,0,n,0);
		@include padding-end(18px);
		text-indent: 41px;
		font-weight: 600;
		border: 0;
		font-size: inherit;
		height: 100%;
	}
	@media (min-width: 1000px) {
		.show-label {
			opacity: 1;
			visibility: visible;
		}
	}
}
.collection-sort {
	display: block;
	flex: 0 0 205px;
	@media (min-width: 750px) {
		flex: 1;
		min-width: 210px;
	}
	span { 
		@include padding-end(10px);
	}
	summary {
		line-height: 40px;
		pointer-events: none;
		@media (min-width: 1000px) {
			pointer-events: unset;
		}
		svg {
			path {
				fill: var(--text-color);
			}
		}
	}
	.sort-title {
		font-weight: 400;
		color: var(--subtext-color);
		@media (max-width: 374px) {
			@include padding-end(7px);
		}
	}
	.sort-list {
		background: var(--background-color);
		box-shadow: 0px 0px 4px rgba(0,0,0,0.15);
		border-radius: var(--border-radius);
		padding: 10px 0;
		margin-top: 10px;
		label {
			margin-bottom: 0;
		}
		li {
			padding: 5px 15px;
			font-weight: 400;
			transition: 0.25s all ease-in-out;
		}
		li {
			&:hover {
				label {
					color: var(--primary-color);
				}
			}
			input:checked + label {
				color: var(--primary-color);
			}
		}
	}
	select {
		border: 0;
		font-weight: 600;
		line-height: 40px;
		font-size: inherit;
		@include ab(0,n,n,0);
		width: auto;
		background: none;
		min-width: 142px;
		@include padding-end(18px);
	}
	.sort-label {
		opacity: 0;
		visibility: hidden;
		min-width: 142px;
	}
	details {
		>div {
			z-index: 100;
		}
	}
	@media (min-width: 1000px) {
		.sort-label {
			opacity: 1;
			visibility: visible;
		}
	}
}
.collection-view-list {
	margin: 0 -4px;
	button {
		display: flex;
		padding: 12px;
		transition: 0.25s all ease-in-out;
		&.active {
			svg {
				path {
					fill: var(--primary-color);
				}
			}
		}
		@media (min-width: 1000px) {
			padding: 12px 10px;
			&:hover {
				svg {
					path {
						fill: var(--primary-color);
					}
				}
			}
		}
		@media (max-width: 749px) {
			&:nth-child(2) {
				svg path {
					fill: var(--primary-color);
				}
			}
			&.active + button {
				svg path {
					fill: #B3B3B3;
				}
			}
		}
	}
}
.main-products {
	&:not([class*="flex-list-"]) {
		.cart-top main {
			.card-group {
				display: none;
			}
		}
		.cart-bottom {
			.card-image {
				.card-group {
					display: none;
				}
			}
			main {
				.card-group {
					display: flex;
				}
			}
		}
	}
	&[class*="flex-list-"] {
		product-card {
			.card-group {
				button {
					@media (max-width: 374px) {
						font-size: 12px;
						padding-left: 15px;
						padding-right: 15px;
					}
				}
			}
			.card-image {
				.card-group {
					display: none;
				}
			}
		}
	}
	&.flex-list-1 {
		product-card {
			display: flex;
			align-items: center;
			flex-direction: unset;
			padding-bottom: 20px;
			position: relative;
			header {
				flex: 0 0 40%;
				@include padding-end(15px);
			}
			main {
				padding-top: 0;
				flex: 1;
			}
			.card-group {
				margin-top: 10px;
			}
			.main-card {
				justify-content: center;
			}

		}
		>div:not(:last-child) {
			product-card {
				&:before {
					content: "";
					@include ab(n,0,0,n);
					width: 100%;
					background: var(--miscellaneous-color);
					height: 1px;
				}
			}
		}
	}
	&.flex-list-2 {
		@media (max-width: 374px) {
			product-card {
				.card-group {
					button {
						.icon {
							display: none;
						}
						.text {
							padding: 0;
						}
					}
				}
			}
		}
	}
	@media (min-width: 750px) {
		&.flex-list-2 {
			product-card {
				align-items: center;
				flex-direction: unset;
				padding-bottom: 20px;
				position: relative;
				header {
					flex: 0 0 40%;
					@include padding-end(20px);
				}
				main {
					padding-top: 0;
					flex: 1;
				}
				.card-group {
					margin-top: 10px;
				}
				.main-card {
					justify-content: center;
				}
				&:before {
					content: "";
					@include ab(n,0,0,n);
					width: 100%;
					background: var(--miscellaneous-color);
					height: 1px;
				}
			}
			>div:nth-child(odd) {
				product-card {
					&:after {
						content: "";
						@include ab(0,n,n,0);
						width: 1px;
						height: calc(100% - 20px);
						background: var(--miscellaneous-color);
					}
				}
			}
			>div:nth-child(even) {
				product-card:before {
					width: calc(100% + 20px);
					@include ab(n,-20px,n,n);
				}
			}

			>div {
				position: relative;
			}
		}
	}
	@media (min-width: 750px) {
		&.flex-list-1 {
			product-card {
				header {
					flex: 0 0 25%;
					@include padding-end(20px);
				}
			}
			.product-card {
				.card-group {
					cart-form {
						max-width: 155px;
						.btn {
							max-width: 155px;
						}
					}
				}
			}
		}
	}
	@media (min-width: 1000px) {
		.collection-view-list {
			margin: 0;
		}
		&.flex-list-2 {
			.product-card {
				.card-group {
					cart-form {
						max-width: 155px;
						.btn {
							max-width: 155px;
						}
					}
				}
			}
		}
	}
	@media (min-width: 1200px) {
		&.flex-list-1 {
			product-card {
				header {
					flex: 0 0 20%;
				}
			}
		}
	}
}